<template>
  <div class="userAssets" v-loading="load">

    <div class=" flex align between">
      <div class="menmuTitle flex align between">
        <h1 class="hasBack flex align"><i class="el-icon-back hoverTr3" @click="goback()" title="返回"></i>提现记录</h1>
      </div>
      <!-- menmuTitle -->

      <div class=" flex align search">
        
        <el-select v-model="query.coinId" placeholder="请选择币种">
          <el-option  label="全部币种" value=""></el-option>
          <el-option v-for="item in coinOptions" :key="item.value" :label="item.label" :value="item.value">
          </el-option>
        </el-select>


      </div>
      <!-- search -->
    </div>

    <div class="space20"></div>

    <div class="listItem" v-if="tableData.length">
      <table>
        <thead>
          <tr>
            <th class="textLeft">币种</th>
            <th class="textLeft">金额</th>
            <th class="textLeft">提现时间</th>
            <th class="textLeft">确认时间</th>
            <th class="textLeft">地址</th>
            <th class="textLeft">备注</th>
            <th class="textRight">状态</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item,index) in tableData" :key="index">
            <td class="textLeft">{{item.coinName}}</td>
            <td class="textLeft">{{item.amount}}</td>
            <td class="textLeft">{{item.addTime}}</td>
            <td class="textLeft">{{item.handleTime}}</td>
            <td class="textLeft">{{item.address}}</td>
            <td class="textLeft">{{item.msg || '--'}}</td>
            <td class="textRight">
              <el-tag size="mini" v-if="item.state == 'application'">申请中</el-tag>
              <el-tag size="mini" type="warning"  v-else-if="item.state == 'withdrawal'">提现中</el-tag>
              <el-tag size="mini" type="danger"  v-else-if="item.state == 'fail'">提现失败</el-tag>
              <el-tag size="mini" type="success"  v-else-if="item.state == 'success'">已完成</el-tag>
              <el-tag size="mini" type="info" v-else>未知状态</el-tag>
            </td>
          </tr>
        </tbody>
      </table>

    </div>
    <!-- assetsTable -->

     <div v-if="pageInfo.total==0" class="nullData"><span>暂无数据</span></div>

    <div class="textRight" v-if="pageInfo && pageInfo.total>0">
      <div class="space40"></div>
      <el-pagination
        background
        layout="prev, pager, next"
        @current-change="x=>query.pageNum = x"
        :page-size="pageInfo.pageSize"
        :total="pageInfo.total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState,
    mapActions
  } from 'vuex'

  import {
    withdrawList as getList
  } from '../data/apis';

  export default {
    data() {
      return {
        load:false,

        tableData:[],//表格数据
        actionsOptions: [],//操作选择
        query:{
          pageSize:20,
          pageNum:1,
          type:'',
          coinId:'',
          startTime:'',
          endTime:''
        },
        pageInfo:''//分页信息
      }
    },
    computed: {
      ...mapState(['userInfo']),
      coinOptions(){
        return this.userInfo.balanceModels.map(v=>{
          return {value:v.coin.id,label:v.coin.coinName}
        })
      }//coinOptions
    },
    methods: {
      getData(){//获取数据
        this.dataList = [];
        this.load = true;
        getList(this.query).then(v=>{
          let {list} = v.data;
          this.tableData = list;
          this.pageInfo = v.data;
          this.load = false;
        })
      },//getData
    },
    watch:{

      query:{
        deep:true, 
        handler:function(newV,oldV){
          this.getData();
        }
      }//query

    },//watch
    mounted() {
      this.query.coinId = this.$route.query.coinId ? Number(this.$route.query.coinId) : '';
      this.getData();
    } //mounted
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";

  .hasBack {
    i {
      color: $them_color;
      font-size: 20px;
      margin-right: 20px;
      cursor: pointer;
    }
  }

  .search {
    margin-top: 20px;

    .el-select {
      margin-right: 20px;
    }
  }

</style>



